.container {
  max-width: 500px;
  margin: auto;
  width: 100%;
}

.game {
  padding: 20px;
  background: #fefefe;
  margin-bottom: 20px;
  border: 1px solid #cecece;
  border-radius: 3px;
  width: 100%;
  & > *:last-child {
    margin-bottom: 0;
  }
}

.gameName {
  margin-bottom: 20px;
  font-size: 1.2em;
}

.removeBtn {
  cursor: pointer;
  position: absolute;
  font-size: 20px;
  right: 20px;
  top: 20px;
}
